import { Injectable } from '@angular/core';
import { Http, Response, URLSearchParams, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

import { Device } from '@ionic-native/device';
import { Setting } from '../app/app.setting';
import { DealRecord } from '../models/dealRecord';

/*
  Generated class for the Order provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular 2 DI.
*/
@Injectable()
export class DealService {
  private DealUrl = this.setting.apiTestHost + '/artradeApp/sold/querysold.action';
  constructor(public http: Http, private setting: Setting, private device: Device) {

  }

  private getDealList(currentPage: number, pageSize: number, memberId: string, type: string, dateType?: string, dateVal?: string, itemName?: string, itemId?: number, glideMark?: number, lotId?: number, authorName?: string, itemAge?: string): Observable<DealRecord[]> {
    let params = new URLSearchParams();
    let queryData = {
      filterMap: {
        memberId: memberId,
        /* type
        * "bNoOrder"：查询(买家)成交未生成订单拍品
        * "bAndOrder"：查询(买家)成交且生成订单拍品
        * "bAll"：查询(买家)所有成交拍品
        * "sNoOrder"：查询(卖家)成交未生成订单拍品
        * "sAndOrder"：查询(卖家)成交且生成订单拍品
        * "lotOccupyMargin":查询(买家)保证金占用的明细信息
        * type为空时查询(买家)成交未生成订单拍品"
        */
        type: type,
        dateType: dateType,
        dateVal: dateVal,
        itemName: itemName || '',
        itemId: itemId || '',
        glideMark: glideMark || '',
        lotId: lotId || '',
        authorName: authorName || '',
        itemAge: itemAge || ''
      },
      pageInfo: {
        currentPage: currentPage,
        pageSize: pageSize
      }
    };
    params.set('imei', this.device.uuid || '0');
    params.set('memberId', memberId || '0');
    params.set('queryData', JSON.stringify(queryData));

    let options = new RequestOptions({
      headers: new Headers(),
      search: params
    });
    return this.http.get(this.DealUrl, options)
      .map(res => {
        if (res.json().result) {
          return res.json().data.data;
        }
        return null;
      })
      .catch(this.handleError);
  }

  //查询(买家)成交未生成订单拍品
  getUnOrderedDealList(currentPage: number, pageSize: number, memberId: string, dateType?: string, dateVal?: string, itemName?: string, itemId?: number, glideMark?: number, lotId?: number, authorName?: string, itemAge?: string): Observable<DealRecord[]> {
    return this.getDealList(currentPage, pageSize, memberId, 'bNoOrder', dateType, dateVal, itemName, itemId, glideMark, lotId, authorName, itemAge);
  }

  //查询(买家)成交且生成订单拍品
  getOrderedDealList(currentPage: number, pageSize: number, memberId: string, dateType?: string, dateVal?: string, itemName?: string, itemId?: number, glideMark?: number, lotId?: number, authorName?: string, itemAge?: string): Observable<DealRecord[]> {
    return this.getDealList(currentPage, pageSize, memberId, 'bAndOrder', dateType, dateVal, itemName, itemId, glideMark, lotId, authorName, itemAge);
  }

  //查询(买家)所有成交拍品
  getAllDealList(currentPage: number, pageSize: number, memberId: string, dateType?: string, dateVal?: string, itemName?: string, itemId?: number, glideMark?: number, lotId?: number, authorName?: string, itemAge?: string): Observable<DealRecord[]> {
    return this.getDealList(currentPage, pageSize, memberId, 'bAll', dateType, dateVal, itemName, itemId, glideMark, lotId, authorName, itemAge);
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }
}



