import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { DealService } from '../../../service/deal';
import { User } from '../../../models/user';
import { DealRecord } from '../../../models/dealRecord';
/**
 * Generated class for the Deal page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-deal',
  templateUrl: 'deal.html',
  providers: [DealService]
})
export class DealPage {

  dealType: string;

  unOrderPageNumber: number = 0;
  unOrderPageSize: number = 5;

  orderPageNumber: number = 0;
  orderPageSize: number = 5;

  user: User;

  unOrderList: DealRecord[] = [];
  orderList: DealRecord[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, private dealService: DealService, private toaCtrl: ToastController) {
    this.dealType = 'bNoOrder';
    this.user = navParams.get('user');
  }

  ionViewDidLoad() {
    this.getUnOrderDeal();
    this.getOrderDeal();
  }

  getUnOrderDeal() {
    this.dealService.getUnOrderedDealList(this.unOrderPageNumber, this.unOrderPageSize, this.user.memberId).subscribe(result => {
      this.unOrderPageNumber++;
      this.unOrderList = result;
    });


  }

  getOrderDeal() {
    this.dealService.getOrderedDealList(this.unOrderPageNumber, this.unOrderPageSize, this.user.memberId).subscribe(result => {
      this.orderPageNumber++;
      this.orderList = result;
    })
  }

  getMoreUnOrderDeal(infiniteScroll?) {
    this.dealService.getUnOrderedDealList(this.unOrderPageNumber, this.unOrderPageSize, this.user.memberId).subscribe(result => {
      if (result.length > 0) {
        this.unOrderPageNumber++;
        if (infiniteScroll) {
          infiniteScroll.complete();
        }
        this.unOrderList = this.unOrderList.concat(result);
      } else {
        if (infiniteScroll) {
          infiniteScroll.complete();
          infiniteScroll.enable(false);
        }
        this.toaCtrl.create({
          message: '已经到底了o(╯□╰)o',
          duration: 1000,
          //showCloseButton: true,
          dismissOnPageChange: true
        }).present();
      }
    }, error => {
      infiniteScroll.complete();
      this.toaCtrl.create({
        message: '好像有什么不对劲o(╯□╰)o',
        duration: 1000,
        //showCloseButton: true,
        dismissOnPageChange: true
      }).present();
    });


  }

  getMoreOrderDeal(infiniteScroll?) {
    this.dealService.getOrderedDealList(this.orderPageNumber, this.orderPageSize, this.user.memberId).subscribe(result => {
      if (result.length > 0) {
        this.orderPageNumber++;
        if (infiniteScroll) {
          infiniteScroll.complete();
        }
        this.orderList = this.orderList.concat(result);
      } else {
        if (infiniteScroll) {
          infiniteScroll.complete();
          infiniteScroll.enable(false);
        }
        this.toaCtrl.create({
          message: '已经到底了o(╯□╰)o',
          duration: 1000,
          //showCloseButton: true,
          dismissOnPageChange: true
        }).present();
      }
    }, error => {
      infiniteScroll.complete();
      this.toaCtrl.create({
        message: '好像有什么不对劲o(╯□╰)o',
        duration: 1000,
        //showCloseButton: true,
        dismissOnPageChange: true
      }).present();
    });
  }

}



